body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}
html{
  overflow-x:  hidden;
  overflow-y:  hidden;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.background{
  background: url(./img/dsfsdfsdf.png);
  overflow: hidden;
  height: 100vh;
  width: 100%;
  background-position: center;
  background-size: cover;
}

.container {
  width: 90%;
  margin: 0 auto;
}

.header{
  display: flex;
  justify-content: space-between;
  padding: 40px 0;
  align-items: center;
}
.header h1{
  font-size: 22px;
  color: rgb(255, 255, 255);
}
ul{
  display: flex;
  justify-content: space-between;
}
.header li{
  display: inline-block;
  list-style: none;
  margin: 0 40px;
  color: rgb(255, 255, 255);
  font-size: 14px;
  font-weight: lighter;
}
.content{
  margin: 220px auto;
}
.content h1{
  font-size: 70px;
  color: #ff0037;
  margin-bottom: 10px;
}
.content h2{
  font-size: 28px;
  margin-top: 0;
  font-weight: lighter;
  color: rgb(255, 255, 255);
}
.content p{
  font-weight: lighter;
  font-size: 16px;
  color: rgb(255, 255, 255);
}
.content button{
  font-weight: lighter;
  font-size: 16px;
  color: rgb(41, 41, 41);
  border-radius: 20px;
  background: white;
  outline: none;
  border: none;
  padding: 7px 30px;
  font-size: 14px;
  margin: 40px 0;
}
.img{
  float: right;
}
.row{
  width: 100%;
  position: absolute;
  bottom: 0;
  display: flex;
  justify-content: space-between;
}
.contacts{
  margin-top: 185px;
  width: 10%;
  margin-left: 0;
  margin-right: 0;
  margin-bottom: 0;
  padding: 0;
  display: flex;
}
.item{
  display: inline-block;
  list-style: none;
  color: rgb(255, 255, 255);
  font-size: 14px;
  font-weight: lighter;
}
.containerPageWorks{
  width: 55%;
  margin: 0 auto;
  padding: 90px
}
.containerPageWorks h1{
  font-size: 38px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  color: #2b2b2b;
  font-weight: lighter;
  margin-bottom: 130px;
}
.wrapper{
  width: 100vw;
  height: 100vh;
  background: white;
}
.project1{
  height: 40vh;
  width: auto;
}
.rowWorks{
  display: flex;
  justify-content: space-between;
  margin: 40px 0;
  align-items: flex-end;
}

.rowWorks p{
  width: 100%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: lighter;
  font-size: 14px;
  margin: 0;
  margin-bottom: 70px;
}
.rowWorks span{
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: lighter;
  font-size: 14px;
}
.down{
  margin-top: 90px;
  margin-left: auto;
  margin-right: auto;
  width: 20%;
  text-align: center;
}
.backB{
  float: right;
}
.float-right{
  float: right;
}
.p-right{
  text-align: right;
}
.containerPageMore{
  width: 55%;
  margin: 0 auto;
  padding: 90px
}
.containerPageMore h1{
  font-size: 38px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  color: #2b2b2b;
  font-weight: lighter;
  margin-bottom: 130px;
}
.rowMore{
  display: flex;
  justify-content: space-between;
  margin: 40px 0;
  align-items: flex-end;
}
.rowMore p{
  width: 100%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: lighter;
  font-size: 15px;
  margin: 0;
  margin-bottom: 70px;
}
.dima{
  height: 35vh;
}
.dimaMargin{
  margin-bottom: 50px;
}
.border1px{
  height: 1px;
  width: 100%;
  background: #4e4e4e;
  margin: 20vh auto;
}
@media (max-width: 1600px) and (min-width: 1281px) {
  .contacts{
    margin-top: 140px;
  }
  .content{
    margin: 150px auto;
  }
}
@media (max-width: 1280px) and (min-width: 1101px) {
  .row{
    height: 60vh;
  }
  .content{
    margin: 150px auto;
  }
  .content h1{
    font-size: 45px;
  }
  .contacts{
    margin-top: 70px;
  }
  .down{
    margin-top: 20px;
  }
}
@media (max-width: 1100px) and (min-width: 790px) {
  .row{
    height: 60vh;
  }
}
@media (max-width: 789px) and (min-width: 560px) {
  .row{
    height: 40vh;
  }
  .header h1{
    font-size: 15px;
    color: rgb(255, 255, 255);
  }
  .header li{
    margin: 0 20px;
  }
  .contacts{
    width: 25%;
  }
  .content h1{
    font-size: 35px;
  }
  .content{
    margin: 100px auto;
  }
}
@media (max-width: 559px) and (min-width: 555px) {
  .row{
    height: 40vh;
  }
  .header h1{
    font-size: 15px;
    color: rgb(255, 255, 255);
  }
  .header li{
    margin: 0 20px;
  }
  .contacts{
    margin-top: 70px;
    width: 25%;
  }
}
@media (max-width: 554px) and (min-width: 365px) {
  .row{
    height: 40vh;
  }
  .header h1{
    font-size: 12px;
    color: rgb(255, 255, 255);
  }
  .header li{
    margin: 0 20px;
    font-size: 11px;
  }
  .contacts{
    margin-top: 70px;
    width: 25%;
  }
  .content h1{
    font-size: 44px;
    color: rgb(255, 255, 255);
    margin-bottom: 10px;
  }
  .content{
    margin: 80px auto;
  }
  .space{
    height: 200px;
    width: 100%;
  }
}